	import {
		ajax,
	} from '@/static/js/base.js'
	import api from '@/static/js/api.js'
export default {
	data() {
		return {
			array: ['男', '女'],
			xzList: ['天蝎座','白羊座','金牛座','双子座','巨蟹座','狮子座','处女座','天秤座','射手座','摩羯座','水瓶座','双鱼座'],
			eduList: ['小学', '初中', '高中', '大专','本科','研究生','硕士'],
			eduIndex: 0,
			index: 0,
			xzIndex:0,
			formNode:{
				name: '',
				gender: '',
				birthday: '',
				address: '',
				constellation: '请选择星座',
				mobile: '',
				height: '',
				income: '',
				work: '',
				education: '请选择学历',
				marriage: '',
				child: '',
				signature: '',
				others: '',
				standard: '',
				album:[],
				avatar: '',
				age: ''
			},
			genderShow: '请选择性别'
		};
	},
	methods: {
		inputValue(e){
			let formNode = this.formNode;
			let name = e.currentTarget.dataset.name;
			let value = e.detail.value;
			formNode[name] = value;
			this.formNode = formNode;
		},
		genderChange(e){
			this.genderShow = this.array[e.target.value]
			if(e.target.value == 0){
				this.formNode.gender = 1
			}else if(e.target.value == 1){
					this.formNode.gender = 2
			}
		},
		xzChange(e){
			this.formNode.constellation = this.xzList[e.target.value]
		},
		eduChange(e){
			this.formNode.education = this.eduList[e.target.value]
		},
		upload(){
			const that = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								console.log(item);
								if (item.size > 10000000) {
									// if (item.size > 500) {
									uni.showToast({
										title: '上传图片不能大于5兆',
										icon: 'none',
										duration: 2000
									});
								} else {
									uni.uploadFile({
										url: api.home_upload_img,
										filePath: item.path,
										name: 'image',
										formData:{
											'disk': 'blinddates/avatar'
										},
										header: {
											// Authorization: this.$parent.globalData.token
										},
										success: res => {
											resolve({
												path: JSON.parse(res.data).data
											});
										}
									});
								}
							});
						})
					)
						.then(e => {
							uni.hideToast();
							console.log(e);
							that.formNode.avatar = e[0].path
						})
						.catch(err => console.log(err));
				}
			});
		},
		uploadImg() {
			const that = this;
			let count = 6-this.formNode.album.length
			uni.chooseImage({
				count: count,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								console.log(item);
								if (item.size > 10000000) {
									// if (item.size > 500) {
									uni.showToast({
										title: '上传图片不能大于5兆',
										icon: 'none',
										duration: 2000
									});
								} else {
									uni.uploadFile({
										url: api.home_upload_img,
										filePath: item.path,
										name: 'image',
										formData:{
											'disk': 'blinddates/album'
										},
										header: {
											// Authorization: this.$parent.globalData.token
										},
										success: res => {
											resolve({
												path: JSON.parse(res.data).data
											});
										}
									});
								}
							});
						})
					)
						.then(e => {
							uni.hideToast();
							console.log();
							let image = that.formNode.album;
							for (let i = 0; i < e.length; i++) {
								image.push(e[i].path);
							}
							that.formNode.album = image
						})
						.catch(err => console.log(err));
				}
			});
		},
		submit(){
			let formNode = this.formNode
			// album
			// return false
			if(formNode.avatar == ''){
				uni.showToast({
					title: '请上传头像',
					icon: 'none'
				})
				return false
			}
			if(formNode.name == ''){
				uni.showToast({
					title: '请填写姓名',
					icon: 'none'
				})
				return false
			}
			if(formNode.age == ''){
				uni.showToast({
					title: '请填写年龄',
					icon: 'none'
				})
				return false
			}
			if(formNode.gender == '请选择性别'){
				uni.showToast({
					title: '请选择性别',
					icon: 'none'
				})
				return false
			}
			if(formNode.birthday == ''){
				uni.showToast({
					title: '请填写生日',
					icon: 'none'
				})
				return false
			}
			if(formNode.address == ''){
				uni.showToast({
					title: '请填写所在地',
					icon: 'none'
				})
				return false
			}
			if(formNode.constellation == '请选择星座'){
				uni.showToast({
					title: '请选择星座',
					icon: 'none'
				})
				return false
			}
			if(formNode.mobile == ''){
				uni.showToast({
					title: '请填写手机号',
					icon: 'none'
				})
				return false
			}
			if(formNode.mobile.length !='11'){
				uni.showToast({
					title: '请填写正确手机号',
					icon: 'none'
				})
				return false
			}
			if(formNode.height == ''){
				uni.showToast({
					title: '请填写身高',
					icon: 'none'
				})
				return false
			}
			
			if(formNode.income == ''){
				uni.showToast({
					title: '请填写月收入',
					icon: 'none'
				})
				return false
			}
			if(formNode.work == ''){
				uni.showToast({
					title: '请填写工作职位',
					icon: 'none'
				})
				return false
			}
			if(formNode.education == '请选择学历'){
				uni.showToast({
					title: '请选择学历',
					icon: 'none'
				})
				return false
			}
			if(formNode.marriage == ''){
				uni.showToast({
					title: '请填写婚姻状况',
					icon: 'none'
				})
				return false
			}
			if(formNode.child == ''){
				uni.showToast({
					title: '请填写是否考虑要小孩',
					icon: 'none'
				})
				return false
			}
			if(formNode.signature == ''){
				uni.showToast({
					title: '请填写个人签名',
					icon: 'none'
				})
				return false
			}
			if(formNode.others == ''){
				uni.showToast({
					title: '请填写其他资料',
					icon: 'none'
				})
				return false
			}
			if(formNode.standard == ''){
				uni.showToast({
					title: '请填写择偶标准',
					icon: 'none'
				})
				return false
			}
			if(formNode.album.length == 0){
				uni.showToast({
					title: '请上传照片',
					icon: 'none'
				})
				return false
			}
			formNode.album = formNode.album.toString()
			console.log(formNode)
			ajax({
				url: api.addBlindDateInfo,
				type: "POST",
				data: this.formNode,
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					uni.showToast({
						title: res.message,
						icon:'none'
					})
					setTimeout( e =>{
						uni.navigateBack({
							delta:1
						})
					},2000)
					// this.info = res.data
				}else{
					uni.showToast({
						title: res.message,
						icon:'none'
					})
				}
			})
		}
	}
};
